import useWindowSize from '../../hooks/useWindowSize';

const JobCard = ({ title, subheader, children }) => {
  const [collapse, setCollapse] = useState(false);
  const handleCollapse = useCallback(() => {
    setCollapse(prevCollapse => !prevCollapse);
  }, []);

  const styles = useMemo(
    () => ({
      container: css`
        padding: 24px;
        background: linear-gradient(#eceff1, #eceff1);
      `,
      title: css`
        font-size: calc(10px + 1vmin);
      `,
      subheader: css`
        font-size: calc(10px + 0.7vmin);
        color: gray;
      `,
      headerContainer: css`
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
      `,
      icon: css`
        transform: rotate(180deg);
        transition: transform 1s linear;
      `,
      iconReverse: css`
        transform: rotate(0deg);
        transition: transform 1s linear;
      `
    }),
    []
  );
  return (
    <Paper css={styles.container}>
      <div css={styles.headerContainer}>
        <div css={styles.titleContainer}>
          <div css={styles.title}>{title}</div>
          <div css={styles.subheader}>{subheader}</div>
        </div>
        <IconButton onClick={handleCollapse}>
          <ExpandMoreIcon css={collapse ? styles.icon : styles.iconReverse} />
        </IconButton>
      </div>
      <Collapse in={collapse}>
        <div css={styles.content}>{children}</div>
      </Collapse>
    </Paper>
  );
};

