/** @jsx jsx */
import { memo, useMemo } from 'react';
import { jsx, css } from '@emotion/core';
import useWindowSize from '../../hooks/useWindowSize';

const EducationPanel = () => {
  const windowSize = useWindowSize();
  const isHorizontal = useMemo(() => windowSize.width > windowSize.height, [
    windowSize.height,
    windowSize.width
  ]);

  const styles = useMemo(
    () => ({
      title: css`
        font-size: calc(10px + 2vmin);
        font-weight: bold;
        border-bottom: ${isHorizontal ? '3px' : '2px'} solid #9e9e9e;
        margin-bottom: 12px;
      `,
      font: css`
        font-size: calc(10px + 0.7vmin);
      `,
      container: css`
        display: flex;
        flex-direction: column;
        height: 100%;
      `,
      content: css`
        flex: 1;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
      `
    }),
    [isHorizontal]
  );
  return (
    <div css={styles.container}>
      <div css={styles.title}>Education</div>
      <div css={[styles.content, styles.font]}>
        <li>2015.09 - 2017.06 中原大學 資訊工程學系 碩士</li>
        <li>2011.09 - 2015.06 中原大學 資訊工程學系 學士</li>
      </div>
    </div>
  );
};

export default memo(EducationPanel);
