/** @jsx jsx */
import { memo, useMemo } from 'react';
import { jsx, css } from '@emotion/core';
import useWindowSize from '../../hooks/useWindowSize';

const ActivityPanel = () => {
  const windowSize = useWindowSize();
  const isHorizontal = useMemo(() => windowSize.width > windowSize.height, [
    windowSize.height,
    windowSize.width
  ]);

  const styles = useMemo(
    () => ({
      title: css`
        font-size: calc(10px + 2vmin);
        font-weight: bold;
        border-bottom: ${isHorizontal ? '3px' : '2px'} solid #9e9e9e;
        margin-bottom: 12px;
      `,
      font: css`
        font-size: calc(10px + 0.7vmin);
      `,
      titleFont: css`
        font-size: calc(10px + 1vmin);
      `,
      content: css`
        display: grid;
        grid-template-rows: auto auto;
        grid-template-columns: 1fr;
      `,
      horizontalContent: css`
        display: grid;
        grid-template-rows: 1fr;
        grid-template-columns: 1fr 1fr;
      `
    }),
    [isHorizontal]
  );
  return (
    <div>
      <div css={styles.title}>Activity</div>
      <div css={isHorizontal ? styles.horizontalContent : styles.content}>
        <div>
          <div css={styles.titleFont}>外訓</div>
          <div css={styles.font}>
            <li>2019 台北國際自動化工業大展</li>
            <li>2019 台北國際電腦展 Computex Taipei</li>
            <li>2018 AppWorks Demo Day</li>
            <li>2018 Javascript 開發者年會 (JSDC)</li>
            <li>2017 Javascript 開發者年會 (JSDC)</li>
          </div>
        </div>
        <div>
          <div css={styles.titleFont}>內訓</div>
          <div css={styles.font}>
            <li>設計思考 Design Thinking</li>
            <li>專案管理 Project Management</li>
          </div>
        </div>
      </div>
    </div>
  );
};

export default memo(ActivityPanel);
